.arrow-icon {
  transform-origin: transform;
  transition-duration: var(--tr-duration);
  transition-timing-function: var(--tr-timing-fn);

  &.top {
    transform: rotate(90deg);
  }
  &.right {
    transform: rotate(180deg);
  }
  &.bottom {
    transform: rotate(-90deg);
  }
  &.left {
    transform: rotate(0deg);
  }
}